@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;700&display=swap);
:root {
  --color-primary-1: #222831;
  --color-primary-2: #393e46;
  --color-primary-3: #00adb5;
  --color-primary-5: #eeeeee;
  --color-primary-4: #171717;
}

body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

hr {
  margin: auto;
  width: 5rem;
  border-color: #393e46;
  border-color: var(--color-primary-2);

  margin-top: 100px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


* {
  padding: 0;
  margin: 0;
}
nav {
  padding: 2rem;
  margin: 2rem;
}
.navbar-container {
  background-color: white;
}
.logo {
  width: 50px;
}
.icon-menu-open {
  left: 90%;
  color: var(--color-primary-2);
  z-index: 20;
  position: fixed;
  font-size: 2rem;
}

.icon-menu-open:hover {
  color: var(--color-primary-3);
}

.icon-menu-close {
  left: 90%;
  position: fixed;
  color: white;
  font-size: 2rem;
  z-index: 25;
  -webkit-animation: spin 2s;
          animation: spin 2s;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

.icon-menu-close:hover {
  color: var(--color-primary-1);
}

button {
  letter-spacing: 5px;
  font-size: 2.5rem;
  color: white;
  text-decoration: none;
  text-align: center;
  border: none;
  background: none;
}

button:hover {
  color: var(--color-primary-2);
}

.nav-menu ul {
  padding-top: 20rem;
  letter-spacing: 5px;
  list-style: none;
  text-align: center;
}

.menu-close {
  display: none;
}

.menu-open {
  text-align: right;
  z-index: 10;
  opacity: 90%;
  line-height: 5rem;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background-color: var(--color-primary-3);

  animation: fadeIn 2s;
  -webkit-animation: fadeIn 2s;
  -moz-animation: fadeIn 2s;
  -o-animation: fadeIn 2s;
  -ms-animation: fadeIn 2s;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.9;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.9;
  }
}

* {
  margin: 0;
  padding: 0;
}

.section-container {
  padding: 0 5rem;
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.section-container-1 {
  text-align: center;
  width: 100%;
}

.section-container-2 {
  text-align: center;
  width: 100%;
}
.section-logo {
  font-family: Montserrat;
  margin-bottom: 2rem;
  letter-spacing: 0.1rem;
  padding: 3rem 0;
  line-height: 5rem;
  color: var(--color-primary-2);
  font-size: 3rem;
  text-align: center;
}
h3 {
  font-family: Montserrat 100;
  color: var(--color-primary-2);
  letter-spacing: 0.1rem;
}

.profile-image {
  z-index: 0;
  position: relative;
  margin-top: 5rem;
  object-fit: cover;
  border-radius: 150px;
  width: 15rem;
  height: 15rem;
  box-shadow: 5px 5px var(--color-primary-3);

  animation: slide-in 0.5s forwards;
  -webkit-animation: slide-in 0.5s forwards;
}

@keyframes slide-in {
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@-webkit-keyframes slide-in {
  100% {
    -webkit-transform: translateX(0%);
  }
}

* {
  margin: 0;
  padding: 0;
}

.section2-container {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.section2 {
  height: 70vh;
  width: 100%;
